<template>
  <div class="form-box" style=" margin-bottom:20px; background:#f4f4f4; padding:10px; ">
    <div class="form-item">
      行业类型：
      <!-- <select style=" width:160px; ">
        <option>请选择行业类型</option>
        <option v-for="(i,index) in hyList" :key="index" :value="i.value">{{i.name}}</option>
      </select> -->
      <el-select style=" width:160px; " v-model="activeHy" @change="change">
        <el-option v-for="(i, index) in hyList" :key="index" :label="i.name" :value="i.value">
        </el-option>
      </el-select>
      选择时间：
      <!-- <ul class="list-time">
        <li class="active">今天</li>
        <li>近7天</li>
        <li>近30天</li>
      </ul>
      自定义时间 -->
      <el-date-picker v-model="startTime" type="datetime" value-format="yyyy-MM-dd HH:mm:ss" placeholder="请选择开始时间">
      </el-date-picker> - <el-date-picker v-model="endTime" type="datetime" value-format="yyyy-MM-dd HH:mm:ss"
        placeholder="请选择结束时间">
      </el-date-picker>
      <button class="btn" @click="search"><i class="icon-search"></i>查询</button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      hyList: [],
      activeHy: '-1',
      startTime: '',
      endTime: ''
    }
  },
  created () {
    this.getHy()
  },
  methods: {
    getHy () {
      let self = this
      this.$http.get({
        api: 'A_S_P_BIG_INDUSTRY_LIST',
        params: {},
        callback: (res) => {
          self.hyList = res
        }
      })
    },
    change () {
      this.$emit('setHy', this.activeHy)
    },
    search () {
      this.$emit('setTime', {
        startTime: this.startTime,
        endTime: this.endTime
      })
    }
  }
}

</script>



