<template>
  <div class="panel">
    <div class="panel-heading">
      <div class="panel-title">缺陷案例分析</div>
    </div>
    <div class="panel-body">
      <div class="col-6">
        <table class="table table-striped">
          <tbody>
            <tr>
              <td>相似案例</td>
              <td>{{countData.sameBrand}}</td>
            </tr>
            <tr>
              <td>相同厂商</td>
              <td>{{countData.sameEnterprise}}</td>
            </tr>
            <tr>
              <td>相同品牌</td>
              <td>{{countData.sameModel}}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="col-6">
        <div class="chart" id="chart1" style="height: 300px">
          <!-- <img alt=""
                src="../../../../assets/images/test/125.png" style=" height:200px" /> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    countData: {
      type: Object
    },
    echart: {
      type: Array
    }
  },
  data () {
    return {}
  },
  mounted () {
    this.draw(this.echart)
  },
  methods: {
    draw (a) {
      let d = this.dataFormat(a)
      let option = {
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          x: 'left',
          data: d.title
        },
        series: [{
          name: '数量',
          type: 'pie',
          radius: ['50%', '70%'],
          avoidLabelOverlap: false,
          label: {
            normal: {
              show: false,
              position: 'center'
            },
            emphasis: {
              show: true,
              textStyle: {
                fontSize: '30',
                fontWeight: 'bold'
              }
            }
          },
          labelLine: {
            normal: {
              show: false
            }
          },
          data: d.data
        }]
      }
      let myChart = this.$echarts.init(document.getElementById('chart1'), 'macarons')
      myChart.setOption(option)
    },
    dataFormat (a) {
      let obj = {
        title: [],
        data: []
      }
      a.map(o => {
        obj.title.push(o.name)
        obj.data.push(o)
      })
      return obj
    }
  },
  watch: {
    'echart' () {
      this.draw(this.echart)
    }
  }
}

</script>

<style>
</style>



