<template>
  <div class="panel" style=" margin-top:-10px;">
    <div class="panel-heading" style=" border-top:none">
      <div class="panel-title">
      </div>
    </div>
    <div class="panel-body">
      <div class="defect-tit">“{{activeObj.name}}”</div>
      <div class="form-box" style=" margin-bottom:20px; background:#f4f4f4; padding:10px; ">
        <div class="form-item">
          <ul class="list-time">
            <li class="active">• 相似缺陷案件风险预判</li>
          </ul>
        </div>
      </div>
      <div class="defect">
        <div class="defect-right">
          <div class="col-6">
            <r1 :item="item" />
          </div>
          <div class="col-6">
            <r2 :countData="countData" :echart="echart" />
          </div>
          <div class="col-6">
            <r3 :activeObj="activeObj" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import R1 from './R1'
import R2 from './R2'
import R3 from './R3'
export default {
  props: {
    activeObj: {
      type: Object
    }
  },
  data () {
    return {
      item: [],
      echart: [],
      countData: {}
    }
  },
  components: {
    R1,
    R2,
    R3
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      let self = this
      this.$http.get({
        api: 'A_S_P_RISK_LIST',
        params: {
          productName: this.activeObj.name
        },
        callback: (res) => {
          self.item = res.caseData
          self.echart = res.analyData
          self.countData = res.countData
        }
      })
    }
  }
}

</script>



