<template>
  <div class="col-4">
    <div class="panel">
      <div class="panel-heading">
        <div class="panel-title">&nbsp;</div>
      </div>
      <div class="panel-body">
        <div class="chart" id="infochart_zhpf">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    activeObj: {
      type: Object
    }
  },
  data () {
    return {}
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      let res = {
        indicator: [{
          name: '维保预警值15分',
          max: 15
        },
        {
          name: '投诉预警值15分',
          max: 15
        },
        {
          name: '执法预警值15分',
          max: 15
        },
        {
          name: '抽检预警值15分',
          max: 15
        }
        ],
        data: [
          Number(this.activeObj.weibaoNo),
          Number(this.activeObj.tousuNo),
          Number(this.activeObj.zhifaNo),
          Number(this.activeObj.choujianNo)
        ]
      }
      this.draw(res)
    },
    draw (a) {
      let option = {
        title: {
          text: '综合预警值'
        },
        radar: {
          name: {
            textStyle: {
              color: '#fff',
              backgroundColor: '#999',
              borderRadius: 3,
              padding: [3, 5]
            }
          },
          indicator: a.indicator
        },
        series: [{
          name: '预警值',
          type: 'radar',
          data: [{
            value: a.data,
            name: '预警'
          }]
        }]
      }
      let myChart = this.$echarts.init(document.getElementById('infochart_zhpf'), 'macarons')
      myChart.setOption(option)
    }
  },
  watch: {
    activeObj: {
      deep: true,
      handler (val, oldVal) {
        this.init()
      }
    }
  }
}

</script>



