<template>
  <div class="panel">
    <div class="panel-heading">
      <div class="panel-title">可能导致结果</div>
    </div>
    <div class="panel-body">
      <div class="chart" id="chart2">
        <!-- <img alt="" src="../../../../assets/images/test/126.png"
              style=" height:200px" /> -->
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    activeObj: {
      type: Object
    }
  },
  data () {
    return {}
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      let self = this
      this.$http.get({
        api: 'A_S_P_RISK_RESULT',
        params: {
          productName: this.activeObj.name
        },
        callback: (res) => {
          if (res.length === 0) {
            document.querySelector('#chart2').innerHTML = '暂无数据'
          } else {
            self.drawBar(res)
          }
        }
      })
    },
    drawBar (a) {
      let option = {
        tooltip: {
          show: true,
          formatter: '{b0}'
        },
        series: [{
          name: '事件画像',
          type: 'wordCloud',
          size: ['80%', '80%'],
          textRotation: [0, 45, 90, -45],
          textPadding: 0,
          autoSize: {
            enable: true,
            minSize: 14
          },
          textStyle: this.createRandomItemStyle(),
          data: a
        }]
      }
      let myChart = this.$echarts.init(document.getElementById('chart2'), 'macarons')
      myChart.setOption(option)
    },
    createRandomItemStyle () {
      return {
        normal: {
          color: function color () {
            return (
              'rgb(' + [
                Math.round(Math.random() * 160),
                Math.round(Math.random() * 160),
                Math.round(Math.random() * 160)
              ].join(',') +
                ')'
            )
          }
        }
      }
    }
  }
}

</script>

<style>
</style>



