<template>
  <div class="panel">
    <div class="panel-heading">
      <div class="panel-title">缺陷库案例智能推荐</div>
    </div>
    <div class="panel-body" style=" min-height:586px; overflow-y:scroll; height:586px">
      <div class="newslist">
        <ul>
          <li v-for="(i,index) in item" :key="index">
            <h3>{{i.title}}</h3>
            <p>来源：{{i.source}}</p>
            <p>生产厂商：{{i.manufacturer}} <span class="badge badge-danger">{{i.type}}</span></p>
            <p>{{i.desc}}</p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    item: {
      type: Array
    }
  },
  data () {
    return {}
  },
  mounted () {},
  methods: {}
}

</script>

<style>
</style>



