<template>
  <div class="col-4">
    <div class="panel">
      <div class="panel-heading">
        <div class="panel-title">多维度走势监控</div>
      </div>
      <div class="panel-body">
        <div class="chart" id="infochart_dwdjk">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    activeObj: {
      type: Object
    },
    startTime: {
      type: String
    },
    endTime: {
      type: String
    }
  },
  data () {
    return {}
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      let self = this
      this.$http.get({
        api: 'A_S_P_QUANTITATIVE_TRENDS',
        params: {
          industryId: -1,
          productName: this.activeObj.name,
          startTime: this.startTime,
          endTime: this.endTime
        },
        callback: (res) => {
          if (res.series.length === 0) {
            document.querySelector('#infochart_dwdjk').innerHTML = '暂无数据'
          } else {
            self.drawLine(res)
          }
        }
      })
    },
    drawLine (a) {
      a.series.map(o => {
        o.type = 'line'
      })
      let option = {
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: a.legend
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: a.category
        },
        yAxis: {
          type: 'value'
        },
        series: a.series
      }
      let myChart = this.$echarts.init(document.getElementById('infochart_dwdjk'), 'macarons')
      myChart.setOption(option)
    }
  },
  watch: {
    activeObj: {
      deep: true,
      handler (val, oldVal) {
        this.init()
      }
    },
    startTime (val, oldVal) {
      this.init()
    },
    endTime (val, oldVal) {
      this.init()
    }
  }
}

</script>



