<template>
  <div class="panel" style=" margin-top:-10px;">
    <div class="panel-heading" style=" border-top:none">
      <div class="panel-title">

      </div>
    </div>
    <div class="panel-body" style="height: 900px">

      <f1 @setHy="setHy" @setTime="setTime" />

      <f2 :activeHy="activeHy" :startTime="startTime" :endTime="endTime" @setActive="setActive" />

      <f3 :activeHy="activeHy" :startTime="startTime" :endTime="endTime" />

      <f4 :activeHy="activeHy" :startTime="startTime" :endTime="endTime" />

      <f5 :activeHy="activeHy" :startTime="startTime" :endTime="endTime" />

      <f6 :activeHy="activeHy" :startTime="startTime" :endTime="endTime" />

    </div>
  </div>
</template>

<script>
import F1 from './F1'
import F2 from './F2'
import F3 from './F3'
import F4 from './F4'
import F5 from './F5'
import F6 from './F6'
export default {
  data () {
    return {
      activeHy: -1,
      startTime: '',
      endTime: ''
    }
  },
  components: {
    F1,
    F2,
    F3,
    F4,
    F5,
    F6
  },
  created () {},
  methods: {
    setActive (i) {
      this.$emit('setActive', i)
    },
    setHy (i) {
      this.activeHy = i
    },
    setTime (i) {
      this.startTime = i.startTime
      this.endTime = i.endTime
    }
  }
}

</script>



