<template>
  <div class="col-4">
    <div class="panel">
      <div class="panel-heading">
        <div class="panel-title">&nbsp;</div>
      </div>
      <div class="panel-body">
        <div class="chart" id="infochart_wb">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    activeObj: {
      type: Object
    },
    startTime: {
      type: String
    },
    endTime: {
      type: String
    }
  },
  data () {
    return {}
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      let self = this
      this.$http.get({
        api: 'A_S_I_MAP',
        params: {
          id: this.activeObj.id,
          startTime: this.startTime,
          endTime: this.endTime
        },
        callback: (res) => {
          self.drawMap(res)
        }
      })
    },
    drawMap (a) {
      let self = this
      let myChart = this.$echarts.init(document.getElementById('infochart_wb'), 'macarons')
      let mapName = 'chongqing'
      let geoCoordMap = {}
      let idName = {}
      let res = []
      let scatterRes = []

      a.map(o => {
        idName[o.name] = o.value
      })
      /* 获取地图数据 */
      myChart.showLoading()
      let mapFeatures = this.$echarts.getMap(mapName).geoJson.features
      myChart.hideLoading()
      mapFeatures.forEach(function (v) {
        // 地区名称
        let name = v.properties.name
        res.push({
          name: name,
          code: v.id,
          value: idName[v.id] ? idName[v.id] : 0
        })
        scatterRes.push({
          name: name,
          value: v.properties.cp
        })
        // 地区经纬度
        geoCoordMap[name] = v.properties.cp
      })

      let convertData = function (res) {
        let arr = []
        for (let i = 0; i < res.length; i++) {
          let geoCoord = geoCoordMap[res[i].name]
          if (geoCoord) {
            arr.push({
              name: res[i].name,
              value: geoCoord.concat(res[i].value)
            })
          }
        }
        return arr
      }
      let option = {
        tooltip: {
          trigger: 'item',
          formatter: function (params) {
            return `${params.name} 企业总数<br> ${params.value ? params.value : 0}家 `
          }
        },
        visualMap: {
          show: true,
          min: 0,
          max: 500,
          left: 'left',
          top: 'bottom',
          text: ['高', '低'],
          calculable: true,
          seriesIndex: [1],
          inRange: {
            color: ['#00467F', '#A5CC82'] // 蓝绿
          }
        },
        geo: {
          show: true,
          map: mapName,
          label: {
            normal: {
              show: false
            },
            emphasis: {
              show: false
            }
          },
          zoom: 2,
          roam: true,
          itemStyle: {
            normal: {
              areaColor: '#CCCCCC',
              borderColor: '#3B5077'
            },
            emphasis: {
              areaColor: '#F5D529'
            }
          }
        },
        series: [{
          name: '散点',
          type: 'scatter',
          coordinateSystem: 'geo',
          data: convertData(a),
          symbolSize: function (val) {
            let result = val[2] / 10
            return result > 20 ? 20 : result
          },
          label: {
            normal: {
              formatter: '{b}',
              position: 'right',
              show: true
            },
            emphasis: {
              show: true
            }
          },
          itemStyle: {
            normal: {
              color: '#05C3F9'
            }
          }
        }, {
          type: 'map',
          map: mapName,
          geoIndex: 0,
          aspectScale: 0.75, // 长宽比
          showLegendSymbol: false, // 存在legend时显示
          label: {
            normal: {
              show: true
            },
            emphasis: {
              show: false,
              textStyle: {
                color: '#fff'
              }
            }
          },
          zoom: 2,
          roam: true,
          itemStyle: {
            normal: {
              areaColor: '#CCCCCC',
              borderColor: '#3B5077'
            },
            emphasis: {
              areaColor: '#F5D529'
            }
          },
          animation: false,
          data: res
        }, {
          name: '散点',
          type: 'scatter',
          coordinateSystem: 'geo',
          data: scatterRes,
          symbolSize: 1,
          label: {
            normal: {
              formatter: '{b}',
              position: 'center',
              show: true
            },
            emphasis: {
              show: true
            }
          },
          itemStyle: {
            normal: {
              color: '#05C3F9'
            }
          }
        }]
      }
      myChart.setOption(option)
      myChart.on('click', function (params) {
        if (params.data && params.data.name) {
          self.$emit('setActiveArea', params.data)
        }
      })
    }
  },
  watch: {
    activeObj: {
      deep: true,
      handler (val, oldVal) {
        this.init()
      }
    },
    startTime (val, oldVal) {
      this.init()
    },
    endTime (val, oldVal) {
      this.init()
    }
  }
}

</script>



