<template>
  <div class="panel" style=" margin-top:-10px;">
    <div class="panel-heading" style=" border-top:none">
      <div class="panel-title">

      </div>
    </div>
    <div class="panel-body" style="height: 900px">
      <div class="defect-tit">“{{activeObj.name}}”
        <a href="javascript:void(0)" @click="$emit('back')">
          <span class="panel-sub-title"><i class="icon-reply"></i>返回</span>
        </a>
      </div>

      <i1 @setTime="setTime" />

      <i2 :activeObj="activeObj" />

      <i3 :activeObj="activeObj" :startTime="startTime" :endTime="endTime" />

      <i4 :activeObj="activeObj" :startTime="startTime" :endTime="endTime" />

      <i5 :activeObj="activeObj" :startTime="startTime" :endTime="endTime" />

      <i6 :activeObj="activeObj" :startTime="startTime" :endTime="endTime" />

      <i7 :activeObj="activeObj" :startTime="startTime" :endTime="endTime" />
    </div>
  </div>
</template>

<script>
import I1 from './I1'
import I2 from './I2'
import I3 from './I3'
import I4 from './I4'
import I5 from './I5'
import I6 from './I6'
import I7 from './I7'
export default {
  props: {
    activeObj: {
      type: Object
    }
  },
  data () {
    return {
      hyList: [],
      activeHy: -1,
      startTime: '',
      endTime: ''
    }
  },
  components: {
    I1,
    I2,
    I3,
    I4,
    I5,
    I6,
    I7
  },
  created () {},
  methods: {
    setTime (i) {
      this.startTime = i.startTime
      this.endTime = i.endTime
    }
  }
}

</script>

<style>
  .defect-tit {
    font-size: 24px;
    margin-bottom: 20px;
    color: #3c8dbc;
  }
  .panel-sub-title {
    float: right;
    font-size: 12px;
    font-weight: 300;
    color: #058fcd;
  }
</style>



