<template>
  <div class="col-6">
    <div class="panel">
      <div class="panel-heading">
        <div class="panel-title">缺陷产品预警</div>
      </div>
      <div style=" min-height:666px;" class="panel-body">
        <table class="table table-striped">
          <thead>
            <tr>
              <td>预警排名</td>
              <td>产品名称</td>
              <td>预警值</td>
            </tr>
          </thead>
          <tbody>
            <tr class="active01" v-for="(i,index) in item" :key="index">
              <td>{{i.scoreNo}}</td>
              <td><a href="javascript:void(0)" @click="$emit('setActive', i)">{{i.name}}</a></td>
              <td>{{i.score}}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    activeHy: {
      type: Number
    },
    startTime: {
      type: String
    },
    endTime: {
      type: String
    }
  },
  data () {
    return {
      item: []
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      let self = this
      this.$http.get({
        api: 'A_S_F_DEFECT_WARNING',
        params: {
          industryId: this.activeHy,
          startTime: this.startTime,
          endTime: this.endTime
        },
        callback: (res) => {
          self.item = res
        }
      })
    }
  },
  watch: {
    activeHy (val, oldVal) {
      this.init()
    },
    startTime (val, oldVal) {
      this.init()
    },
    endTime (val, oldVal) {
      this.init()
    }
  }
}

</script>



