<template>
  <div class="col-4">
    <div class="panel">
      <div class="panel-heading">
        <div class="panel-title">抽检情况</div>
      </div>
      <div class="panel-body">
        <div class="chart" id="infochart_cj">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    activeObj: {
      type: Object
    },
    startTime: {
      type: String
    },
    endTime: {
      type: String
    }
  },
  data () {
    return {}
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      let self = this
      this.$http.get({
        api: 'A_S_I_CHECKED',
        params: {
          industryId: -1,
          productName: this.activeObj.name,
          startTime: this.startTime,
          endTime: this.endTime
        },
        callback: (res) => {
          if (res.qualified === '0' && res.unqualified === '0') {
            document.querySelector('#infochart_cj').innerHTML = '暂无数据'
          } else {
            self.drawMap(res)
          }
        }
      })
    },
    drawMap (a) {
      let myChart = this.$echarts.init(document.getElementById('infochart_cj'), 'macarons')
      let option = {
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {c} ({d}%)'
        },
        legend: {
          orient: 'vertical',
          x: 'left',
          data: ['合格', '不合格']
        },
        series: [{
          name: '抽检情况',
          type: 'pie',
          radius: ['50%', '70%'],
          avoidLabelOverlap: false,
          label: {
            normal: {
              show: false,
              position: 'center'
            },
            emphasis: {
              show: true,
              textStyle: {
                fontSize: '30',
                fontWeight: 'bold'
              }
            }
          },
          labelLine: {
            normal: {
              show: false
            }
          },
          data: a
        }]
      }
      myChart.setOption(option)
    }
  },
  watch: {
    activeObj: {
      deep: true,
      handler (val, oldVal) {
        this.init()
      }
    },
    startTime (val, oldVal) {
      this.init()
    },
    endTime (val, oldVal) {
      this.init()
    }
  }
}

</script>



