<template>
  <div class="sys-main sys-main-style">

    <div class="catechose">
      <ul>
        <li><a :class="{'active':cmptName=='found'}" @click="cmptName='found'" href="javascript:void(0)">缺陷产品发现</a></li>
        <li v-show="cmptName=='info'||cmptName=='risk'"><a :class="{'active':cmptName=='info'}" @click="cmptName='info'"
            href="javascript:void(0)">缺陷产品状况分析</a></li>
        <li v-show="cmptName=='info'||cmptName=='risk'"><a :class="{'active':cmptName=='risk'}" @click="cmptName='risk'"
            href="javascript:void(0)">潜在风险预判</a></li>
      </ul>
    </div>

    <products-found v-if="cmptName=='found'" @setActive="setActive" />
    <products-info v-if="cmptName=='info'" :activeObj="activeObj" @back="cmptName = 'found'" />
    <products-risk v-if="cmptName=='risk'" :activeObj="activeObj" @back="cmptName = 'found'" />
    <!-- <risk v-if="cmptName=='risk'" /> -->

  </div>
</template>

<script>
import ProductsFound from './components/ProductsFound'
import ProductsInfo from './components/ProductsInfo'
import ProductsRisk from './components/ProductsRisk'
export default {
  data () {
    return {
      flowIsData: 1,
      cmptName: 'found',
      activeObj: {}
    }
  },
  components: {
    ProductsFound,
    ProductsInfo,
    ProductsRisk
  },
  created () {},
  methods: {
    setActive (o) {
      this.activeObj = o
      this.cmptName = 'info'
    }
  }
}

</script>

<style scoped>
  .el-pagination {
    margin-top: 20px;
  }

  .el-pagination .number {
    position: initial;
    width: initial;
  }

  .casesearch li span.el-checkbox__inner,
  .casesearch li span.el-checkbox__input {
    width: 14px;
  }

  .panel-sub-title {
    float: right;
    font-size: 12px;
    font-weight: 300;
    color: #058fcd;
  }

  .casesearch li span.spantitle {
    width: 150px;
  }

  .el-table--fit {
    border-right: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
  }

</style>



